$sibRed: #e30613;
$vpipeLightRed: #feeaeb;
$vpipeOffBlack: #111111;
$mainRadius: 20px;
$mainRadiusTop: 30px 30px 0 0;
$mainRadiusBottom: 0 0 30px 30px;
$smallRadius: 10px;

@font-face {
  font-family: 'CabinetGrotesk-Variable';
  src: url('../fonts/CabinetGrotesk-Variable.woff2') format('woff2'),
    url('../fonts/CabinetGrotesk-Variable.woff') format('woff'),
    url('../fonts/CabinetGrotesk-Variable.ttf') format('truetype');
    font-weight: 100 900;
    font-display: swap;
    font-style: normal;
}

body {

  margin: 0;
  padding: 0;
  box-sizing: border-box;
  color:black;
  overflow-x: hidden;

  &:not(.homebody) {
    padding-top: 130px;
    .container {
      max-width: calc(100% - 150px);
      h2 {
        scroll-margin-top: 150px;
      }
      #funding {
        margin: revert;
      }
    }
    #nav-container {
      top: 0;
      background: #d80c0c0f;
      -webkit-backdrop-filter: blur(4px);
      backdrop-filter: blur(4px);
      &.navScrolled {
        background: #d80c0c0f;
        .container {
          background: transparent;
          box-shadow: none;
        }
      }
      .container {
        padding: 10px 0;
      }
    }
    header {
      &:not(.home) {
        .container {
          max-width: calc(100% - 80px);
        }
      }
    }
  }
  font: {
    family: "Inter", sans-serif;
  }
  a {
    color: black;
  }
  h1, h2 {
    font: {
      family: "CabinetGrotesk-Variable", sans-serif;
    }
  }
  h1 {
    font: {
      size: 2.2em;
      weight: 400;
    }
    span {
      font: {
        weight: 700;
      }
    }
  }
  
  #btn-getting-started {
    display: flex;
    padding: 15px 25px;
    position: fixed;
    right: 25px;
    bottom: 25px;
    border-radius: 50px;
    background-image: linear-gradient(to right, #111, #444, #111, #444);
    box-shadow: 0 4px 15px 0 rgba(116, 79, 168, 0.75);
    background-size: 300% 100%;
    border-radius: 50px;
    transition: all .4s ease-in-out;
    color: white;
    text-decoration: none;
    &:hover {
      background-position: 100% 0;
      transition: all .4s ease-in-out;
    }
    img {
      width: 20px;
      display: none;
      margin: 0 0 0 8px;
    }
  }

  .container {
    width: 100%;
    max-width: calc(100% - 110px);
    margin: 0 auto;
    padding: 0 40px;
  }

  #nav-container {
    width: 100%;
    display: flex;
    justify-content: end;
    position: fixed;
    top: 40px;
    z-index: 9999;
    .container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .logos {
        width: inherit;
        a {
          img {
            width: 150px;
            padding-right: 12px;
          }
          &:last-child {
            img {
              width: 52px;
              padding-left: 20px;
              border-left: 1px solid #0000002b;
            }
          }
        }
      }
      button {
        width: 33%;
        display: none;
      }

      ul {
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        list-style: none;
    
        li {
          position: relative;
          margin: 0 20px;
          padding: 10px 0;
          line-height: 30px;
          margin-top: -2px;
          margin-bottom: -2px;
          img {
            width: 10px;
            position: absolute;
            top: 50%;
            right: -4px;
            margin-top: -2px;
          }

          a {
            padding: 10px;
            color: black;
            text-decoration: none;
    
            &:hover {
              color: $sibRed;
            }
          }

          ul {
            min-width: max-content;
            padding: 20px 10px;
            display: none;
            position: absolute;
            top: 100%;
            left: 10px;
            z-index: 100;
            background: white;
            border-radius: $smallRadius;
            box-shadow: 0 0 30px -5px rgba(0,0,0,0.25);
            li {
              margin: 0 5px;
              padding: 0;
              line-height: 24px;
              a {
                display: block;
                padding: 10px 5px;
                color: black;
                text-decoration: none;
                border-radius: 5px;
                &:hover {
                  background: $vpipeOffBlack;
                  color: white;
                }
              }
            }
          }
    
          &:hover {
            ul {
              display: block;
            }
          }

          &:last-child, &:nth-last-child(2) {
            ul {
              left: auto;
              right: 0;
            }
          }
        }
      }
    }
    &.navScrolled {
      -webkit-backdrop-filter: blur(4px);
      backdrop-filter: blur(4px);
      .container {
        background: rgba(255, 255, 255, 0.9);
        box-shadow: 0 0 20px -10px #00000054;
        border-radius: $mainRadius;
      }
    }
  }
  
  header {
    height: auto;
    padding: 180px 20px 40px 20px;
    position: relative;
    background: rgb(255,244,244);
    background: linear-gradient(328deg, rgba(231,238,245,.9) 0%, rgba(255,237,237,.9) 100%);
    -webkit-backdrop-filter: blur(4px);
    backdrop-filter: blur(4px);
    border-radius: $mainRadiusBottom;
    .container {
      .hero-carousel {
        width: 100%;
        min-height: 440px;
        margin: 60px 0;
        position: relative;
        .slide {
          display: flex !important;
          color: black;
          .image {
            flex: 1;
            img {
              width: 100%;
              max-width: 730px;
              height: auto;
              border-radius: 10px;
            }
          }
          .video {
            width: 100%;
            flex: 1;
            position: relative;
            padding-bottom: 29.2%;
            overflow: hidden;
            iframe {
              width: 100%;
              height: 100%;
              position: absolute;
              top: 0;
              left: 0;
              border-radius: $smallRadius;
            }
          }
          .content {
            width: 50%;
            padding: 20px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: start;
            flex: 1;
            h1 {
              font: { 
                size: 2em;
                weight: 700;
              }
              margin: 5px 0;
            }
            p {
              font-size: 1.2em;
              margin: 5px 0 20px;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-line-clamp: 3; /* Number of lines to show */
              -webkit-box-orient: vertical;
            }
            .cta {
              width: auto;
              display: inline-flex;
              flex-grow: 0;
              padding: 15px 30px;
              background: $sibRed;
              color: #fff;
              text-decoration: none;
              border-radius: 5px;
              transition: background 0.3s ease;
              &:hover {
                background: $vpipeOffBlack;
              }
            }
          }
        }
      }

      .slider-dots {
        width: 250px;
        margin: 40px auto;
        padding: 0 0 10px 0;
        text-align: center;
        list-style: none;
        background: white;
        border-radius: $mainRadius;
        
        li {
          width: calc(25% - 8px);
          display: inline-block;
          margin: 0 2px;
        }
        
        button {
          width: 100%;
          height: 8px;
          position: relative;
          border: 0;
          background-color: #ccc;
          font-size: 0;
          border-radius: 5px;
          cursor: pointer;
          transition: background-color .5s;
          &:hover {
            background-color: #999;
          }
          &:after {
            width: 0%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            content: '';
            display: block;
            border-radius: 5px;
          }
        }
        
        .slick-active {
          button { 
            &:after {
              background-color: red;
              animation: progress var(--autoplay-speed) linear forwards;
            }
            &.resetTimer {
              animation: pulsate 1s infinite alternate;
              &:after {
                animation: resetBar 1s infinite alternate;
                // animation-play-state: paused;
              }
              @keyframes pulsate {
                from {
                  width: 20%;
                  opacity: 1;
                }
                to {
                  width: 70%;
                  opacity: .3;
                }
              }
              @keyframes resetBar {
                from {
                  width: 100%;
                }
                to {
                  width: 100%;
                }
              }
            }
          }
        }
      }
      
      @keyframes progress {
        from {
          width: 0%;
        }
        
        to {
          width: 100%;
        }
      }
    
      // .slide-indicators {
      //   display: flex;
      //   padding: 8px;
      //   position: absolute;
      //   bottom: 30px;
      //   left: 50%;
      //   transform: translateX(-50%);
      //   background: white;
      //   border-radius: 10px;
      //   .indicator {
      //     display: inline-block;
      //     width: 30px;
      //     height: 8px;
      //     background: #ccc;
      //     border-radius: 10px;
      //     margin: 0 5px;
      //     cursor: pointer;
      //     &.active {
      //       background: $sibRed;
      //     }
      //   }
      // }
    }
    &:not(.home) {
      min-height: auto;
      margin: 40px;
      padding: 0;
      background: #dffff8;
      border-radius: $mainRadius;
    }
  }

  #get-started {
    margin: 160px 0;
    position: relative;
    .metaball {
      position: absolute;
      right: 50px;
      top: -275px;
      z-index: -1;
      opacity: 0;
      animation: fadeInMoveDown 2s ease-in-out forwards;
  
      img {
        width: 250px;
      }
    
      &:first-child {
        left: 10px;
        right: auto;
        top: -320px;
        animation: fadeInMoveUp 1s ease-in-out forwards;
        animation-delay: 1s;

        img {
          width: 280px;
        }

      }
    }
    
    @keyframes fadeInMoveDown {
      from {
        opacity: 0;
        transform: translateY(-40px);
      }
      to {
        opacity: 1;
        transform: translateY(0);
      }
    }
    
    @keyframes fadeInMoveUp {
      from {
        opacity: 0;
        transform: translateY(40px);
      }
      to {
        opacity: 1;
        transform: translateY(0);
      }
    }    
    
    h1 {
      padding: 20px 0;
      text-align: center;
    }
    .container {
      justify-content: space-between;
      .homepage-columns {
        display: flex;
        justify-content: space-evenly;
        gap: 20px;
        .column {
          width: 25%;
          padding: 20px 20px 80px 20px;
          position: relative;
          // background: #c0fbea;
          background: $vpipeLightRed;
          background: linear-gradient(119deg, rgba(254,234,235,1) 0%, rgba(235,220,221,1) 100%);
          border-radius: $smallRadius;
          .column-heading {
            padding: 20px;
            text-align: center;
            background: rgba(255, 255, 255, 0.8);
            border-radius: $smallRadius;
            img {
              height: 55px;
            }
            h2 {
              margin: 0;
              font: {
                weight: 400;
              }
            }
          }
          h4 {
            display: inline-block;
            margin: 20px 0 0;
            padding: 2px 5px;
            font: {
              size: 1em;
              weight: 300;
            }
            background: $sibRed;
            color: white;
            border-radius: 10px;
          }
          ul {
            padding: 0 0 0 20px;
            li {
              padding: 8px 0;
              list-style-image: url(../img/ic-list-style.svg);
              a {
                color: black;
                text-decoration: none;
                &:hover {
                  color: $sibRed;
                }
              }
            }
          }
          &:after {
            content: '';
            width: 100px;
            height: 8px;
            position: absolute;
            bottom: 25px;
            left: 0;
            right: 0;
            margin-left: auto;
            margin-right: auto;
            // background: rgba(0, 0, 0, 0.148);
            background: #ff00001a;
            border-radius: $smallRadius;
          }
        }
      }
    }
  }

  #vpipe-numbers {
    margin: 220px 0 70px;
    position: relative;
    text-align: center;
    .dna-txt {
      width: 48%;
      overflow: hidden;
      position: absolute;
      top: -40px;
      left: 0;
      // color: $sibRed;
      background: linear-gradient(to right, rgba(226,6,19,.5) 0%, rgba(226,6,19,0) 100%);
      -webkit-background-clip: text;
      background-clip: text;
      color: transparent;
      font: {
        family: "Courier";
        size: 1.2em;
      }
      &:nth-child(2) {
        left: auto;
        top: 0px;
        right: 0;
        background: linear-gradient(to left, rgba(226,6,19,.5) 0%, rgba(226,6,19,0) 100%);
        -webkit-background-clip: text;
        background-clip: text;
        color: transparent;
      }
    }
    .bg-full-width {
      background: rgb(255,244,244);
      background: linear-gradient(328deg, rgba(255, 244, 244, 0.7) 0%, rgba(231, 238, 245, 0.7) 100%);
      -webkit-backdrop-filter: blur(4px);
      backdrop-filter: blur(4px);
      border-radius: $mainRadius;
      .container {
        display: flex;
        gap: 20px;
        margin: 80px auto;
        padding: 180px 0;
        .column-numbers {
          width: 33%;
          padding: 40px 0;
          display: flex;
          justify-content: center;
          align-items: center;
          background: rgba(255,255,255,0.4);
          border-radius: $smallRadius;
        }
      }
    }
  }

  .abstract-adeno-virus {
    position: absolute;
    left: -130px;
    z-index: -1;
    img {
      width: 400px;
      transform: rotate(-38deg);
    }
  }

  #why-vpipe {
    padding: 160px 0;
    text-align: center;
    .container {
      display: flex;
      gap: 20px;
      margin: 20px auto;
      .column {
        width: 25%;
        padding: 60px 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background: #ffffffcf;
        -webkit-backdrop-filter: blur(4px);
        backdrop-filter: blur(4px);
        border: 1px solid #eee;
        border-radius: $smallRadius;
        .img-container {
          height: 80px;
          display: flex;
          align-items: end;
          justify-items: center;
        }
        h2 {
          font: {
            weight: 300;
          }
          margin-bottom: 0;
        }
      }
    }
  }

