  #funding {
    margin: 160px 0;
    h1 {
      text-align: center;
    }
    .container {
      display: flex;
      justify-content: space-evenly;
      gap: 20px;
      .column {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 40px 0;
        border: 1px solid #ddd;
        border-radius: $smallRadius;
        a {
          img {
            width: 200px;
          }
        }
      }
    }
  }

  #footer {
    margin: 160px 0 0;
    padding: 0 0 20px;
    background: rgba(30, 118, 173, .169);
    background: linear-gradient(119deg, rgba(217,232,241,1) 42%, rgba(235,220,221,1) 100%);
    border-radius: $mainRadiusTop;
    .container {
      padding: 80px 0 0;
      .row {
        display: flex;
        justify-content: space-between;
        gap: 20px;
        .column {
          padding: 20px;
          h1 {
            font: {
              size: 1.4em;
              weight: 700;
            }
          }
          ul {
            padding: 0 0 0 17px;
            li {
              margin: 8px 0;
              padding: 5px 0;
              list-style-image: url(../img/ic-list-style.svg);
              a {
                color: black;
                text-decoration: none;
                &:hover {
                  color: $sibRed;
                }
              }
            }
          }
        }
        &:nth-child(2) {
          margin: 80px 0;
          .column {
            width: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-direction: column;
            border: 1px solid rgba(0, 0, 0, 0.2);
            border-radius: $smallRadius;
            a {
              &:hover {
                color: $sibRed;
              }
            }
            h1 {
              margin-top: 0;
            }
            .social-container {
              display: flex;
              a {
                width: 40px;
                height: 40px;
                margin: 0 10px;
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 10px;
                background: rgba(255, 255, 255, 1);
                border-radius: $smallRadius;
                transition: box-shadow .3s ease;
                &:hover {
                  box-shadow: 0 0 40px -20px black;
                }
              }
            }
          }
        }
        &:last-child {
          justify-content: center;
        }
      }
    }
  }

}

@media screen and (min-width: 1600px) {
  body {
    .container {
      width: 100%;
      max-width: 1500px;
      margin: 0 auto;
      padding: 0 40px;
    }
    header {
      min-height: 590px;
    }
  }
}

@media screen and (max-width: 1350px) {
  body {
    header {
      .container {
        .hero-carousel {
          min-height: 380px;
        }
      }
    }
    #meet-the-team {
      .container {
        display: grid;
        .column {
          width: auto;
          margin: 10px 0;
        }
      }
    }
  }
}

@media screen and (max-width: 1218px) {
  body {
    #get-started {
      .container {
        .homepage-columns {
          .column {
            .column-heading {
              h2 {
                font: {
                  size: 1em;
                }
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  body {
    .container {
      max-width: calc(100% - 10px);
      padding: 0;
    }
    #nav-container {
      .container {
        max-width: calc(100% - 50px);
        padding: 10px;
        .logos {
          a {
            img {
              width: 120px;
              padding-right: 5px;
            }
            &:last-child {
              img {
                width: 40px;
                padding-left: 10px;
              }
            }
          }
        }
        nav {
          display: none;
          &.show {
            display: block;
            width: auto;
            position: absolute;
            top: 80px;
            right: 20px;
            z-index: 9999;
            background: white;
            box-shadow: 0 0 20px -10px rgba(0, 0, 0, 0.6);
            border-radius: $smallRadius;
            &:before {
              content: "";
              position: absolute;
              top: -10px;
              right: -10px;
              transform: translateX(-50%);
              border-left: 20px solid transparent;
              border-right: 20px solid transparent;
              border-bottom: 20px solid white;
            }
          
            ul {
              display: block;
              li {
                img {
                  display: none;
                }
                span {
                  display: none;
                }
                ul {
                  padding: 10px;
                  display: block;
                  position: unset;
                  box-shadow: none;
                  li {
                    a {
                      padding: 5px;
                    }
                  }
                }
              }
            }
          }          
        }
        button {
          display: flex;
          align-items: end;
          flex-direction: column;
          background: none;
          border: none;
          cursor: pointer;
          span {
            width: 35px;
            height: 2px;
            display: block;
            background: $sibRed;
            margin: 4px 0;
            border-radius: $smallRadius;
            &:nth-child(2) {
              position: relative;
              left: 5px;
            }
          }
        }
      }
    }
    header {
      padding-top: 130px;
      .container {
        .hero-carousel {
          .slide.active {
            display: block;
            .content {
              width: 100%;
              padding-left: 0;
            }
            .video {
              padding-bottom: 56%;
            }
          }
        }
      }
    }
    #get-started {
      margin-top: 80px;
      .container {
        .homepage-columns {
          display: grid;
          grid-template-columns: 50% 50%;
          grid-row: auto auto;
          grid-column-gap: 20px;
          grid-row-gap: 20px;
          justify-content: space-around;
          .column {
            width: auto;
            .column-heading {
              h2 {
                font: {
                  size: 1.2em;
                }
              }
              img {
                height: 45px;
              }
            }
          }
        }
      }
    }
    #vpipe-numbers {
      .bg-full-width {
        .container {
          .column-numbers {
            h1 {
              font: {
                size: 1.4em;
              }
            }
          }
        }
      }
    }
    .abstract-adeno-virus {
      opacity: .2;
    }
    #why-vpipe {
      .container {
        display: grid;
        grid-template-columns: 50% 50%;
        grid-column-gap: 20px;
        grid-row-gap: 20px;
        justify-content: space-around;
        .column {
          width: auto;
          h2 {
            font: {
              size: 1.2em;
            }
          }
        }
      }
    }
    #meet-the-team {
      .container {
        display: block;
        .column {
          width: auto;
          margin: 20px 0;
        }
      }
      .abstract-covid {
        opacity: .2;
      }
    }
    #funding {
      margin: 80px 0;
      padding: 40px 0 0;
      .container {
        .column {
          a {
            img {
              width: 120px;
            }
          }
        }
      }
    }
    #footer {
      margin: 100px 0 0;
      .container {
        .row, .row:nth-child(2) {
          display: grid;
          grid-template-columns: 50% 50%;
          grid-column-gap: 20px;
          grid-row-gap: 20px;
          justify-content: space-around;
          .column {
            width: auto;
            .social-container {
              a {
                width: 20px;
                height: 20px;
                img {
                  width: 18px;
                }
              }
            }
          }
        }
        .row:last-child {
          display: flex;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  body {
    &:not(.homebody) {
      .container {
        max-width: calc(100% - 60px);
      }
      #nav-container {
        .container {
          padding: 20px 0;
        }
      }
      header {
        &:not(.home) {
          margin: 20px 10px 10px 10px;
          .container {
            max-width: calc(100% - 40px);
            padding: 10px 0;
          }
        }
      }
    }
    header {
      .container {
        .hero-carousel {
          margin: 0 0 40px;
          .slide {
            .content {
              h1 {
                font: {
                  size: 1.8em;
                }
              }
            }
          }
        }
        nav {
          &.show {
            top: 95px;
          }
        }
      }
    }
    #get-started {
      margin: 50px 15px 0;
      position: relative;
      z-index: -1;
      .metaball {
        display: none;
        position: absolute;
        z-index: -1;
        img {
          width: 150px;
        }
        &:first-child {
          display: block;
          left: -40px;
          top: -200px;
          img {
            width: 200px;
          }
        }
      }
      .container {
        .homepage-columns {
          display: grid;
          grid-template-columns: 100%;
          grid-row: auto;
          .column {
            width: auto;
            .column-heading {
              h2 {
                font: {
                  size: 1.2em;
                }
              }
              img {
                height: 45px;
              }
            }
          }
        }
      }
    }
    #vpipe-numbers {
      margin: 80px 0 70px;
      .bg-full-width {
        .container {
          display: block;
          padding: 50px 0;
          .column-numbers {
            width: 90%;
            margin: 20px auto;
          }
        }
      }
    }
    #why-vpipe {
      padding: 0;
      .container {
        display: block;
        .column {
          margin: 20px 0;
        }
      }
    }
    #meet-the-team {
      padding: 160px 0 0;
      .container {
        .column {
          ul {
            li {
              font: {
                size: .85em;
              }
            }
          }
        }
      }
    }
    #funding {
      padding: 0;
    }
    #footer {
      .container {
        padding: 30px 0 0;
        .row {
          display: block;
          .column {
            margin: 0;
            padding: 1px 20px;
          }
          &:nth-child(2) {
            display: block;
            margin: 20px;
            .column {
              margin: 20px 0;
              padding: 20px;
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 375px) {
  body {
    #nav-container {
      .container {
        nav {
          &.show {
            ul {
              li {
                ul {
                  display: none;
                }
              }
            }
          }
        }
      }
    }
  }
}